.ContactList {
  width: 380px;
  margin: 0px;
  padding: 0px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

p {
  margin: 0px;
  margin-right: 10px;
}

span {
  margin-left: 20px;
}

button {
  margin-left: 30px;
  border-radius: 5px;
  cursor: pointer;
  background: white;
  border-color: #ececec;
}

button:hover {
  background: #4e8cf0;
}
